// src/components/Projects.js

import React from "react";
import {projects} from "../../data";

import {Link} from "react-router-dom";

export default function Projects() {
    return (
        <section id="projects" className="text-gray-400 bg-gray-900 body-font">
            <div className="container px-5 py-10 mx-auto text-center lg:px-40">
                <div className="flex flex-col w-full mb-20">
                    <h1 className="sm:text-6xl text-3xl font-medium title-font mb-4 text-white">
                        Projects
                    </h1>
                    <p className="lg:w-2/3 mx-auto leading-relaxed text-base">
                        Projects I worked on in solo or with friends
                    </p>
                </div>
                <div className="flex flex-wrap -m-4">
                    {projects.map((project) => (
                        <a key={project.image} className="sm:w-1/2 p-4">
                            <Link to={project.link} className="mr-5 hover:text-white">
                                <h1 className="sm:text-1xl text-2xl font-medium title-font">
                                    {project.title}
                                </h1>
                                <div className="flex relative">
                                    <img
                                        alt="gallery"
                                        className="absolute inset-0 w-full h-full object-cover object-center"
                                        src={project.image}
                                    />
                                    <div
                                        className="px-8 py-10 relative z-10 w-full border-4 border-gray-800 bg-gray-900 opacity-0 hover:opacity-80">
                                        <br/>
                                        <br/>
                                        <br/>
                                        <br/>
                                        <h2 className="tracking-widest text-sm title-font font-medium text-green-400 mb-1">
                                            {project.subtitle}
                                        </h2>
                                        <br/>
                                        <br/>
                                        <br/>
                                        <br/>
                                        <p className="leading-relaxed">{project.description}</p>
                                    </div>
                                </div>
                            </Link>
                        </a>
                    ))}
                </div>
            </div>
        </section>
    );
}


